@import url('https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap');

body {
  background-color: #101010;
  font-family: 'Roboto', sans-serif; /* Ensures the font is consistent across all text elements */
  margin: 0; /* Resets default margin for a clean layout start */
  color: #5a5a5a; /* Sets a default text color that can be inherited */
}

input,
button {
  font-family: inherit; /* Inherits font settings from body for consistency */
  padding: 8px 6px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 16px;
  line-height: 24px;
  width: 100%; /* Full-width inputs */
  max-width: 250px; /* Limit max width for better form control display */
  margin-bottom: 20px;
  background-color: #fff;
}

.App {
  text-align: center;
  padding: 2% 5%;
  color: #333; /* More emphasis on app-specific texts */
  background-color: #FFD859;
}

.App * {
  box-sizing: border-box;
}

.chart-container {
  display: flex;
  flex-wrap: wrap; /* Allows items to wrap on smaller screens */
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); /* Responsive grid items */
  padding-bottom: 20px; 
  padding-top: 20px;
  gap: 20px; /* Adds space between grid items */
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
  color: #5a5a5a;
}


#city-search {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px auto 40px;
  position: relative;
  width: 100%;
}

.city {
  width: 100%;
}

.suggestions {
  list-style: none;
  padding: 0;
  margin: 0;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  background: white;
  border: 1px solid #ccc;
  border-top: none;
  z-index: 10;

}

#number-of-events {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px auto;
}


label[for="eventNumberInput"] {
  color: #5a5a5a;
}

.suggestions {
  width: 250px;
  position: absolute;
  top: 45px;
  z-index: 2;
  background-color: #fff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.suggestions>li {
  text-align: left;
  padding: 10px;
  background-color: #fff;
  cursor: pointer;
}

.suggestions>li:not(:last-child) {
  border-bottom: 1px solid #eee;
}

.suggestions>li:hover {
  background-color: #e9e9e9;
}

.event {
  padding: 20px;
  margin-bottom: 20px;
  text-align: left;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fff;
}

.alerts-container {
  position: fixed;
  top: 0px;
  left: 20px;
  width: 250px;
  z-index: 1000;
}

@media (max-width: 768px) {
  .chart-container {
    grid-template-columns: 1fr; /* One column grid on smaller screens */
  }
}